import React from 'react';
import Rodal from 'rodal';
import T from "i18react";

function Mobile(props) {

    return (
        <Rodal visible={props.open} onClose={props.toggle}>
            <h4>{T.translate('modals.mobile.title')}</h4>
            <p>
                {T.translate('modals.mobile.text')}
            </p>
        </Rodal>
    );
}

export default Mobile;
