import React, {useState} from 'react';

function ToggleDark() {
    let session = sessionStorage.getItem('darkmode') === 'true';
    const [darkModeEnabled, setDarkMode] = useState(session);

    const toggleDarkMode = (event) => {
        sessionStorage.setItem('darkmode',event.target.checked);
        setDarkMode(event.target.checked);
        addDarkToBody(event.target.checked);
    };

    const addDarkToBody = (type) => {
        if(type){
            document.body.classList.add('dark');
        }else{
            document.body.classList.remove('dark');
        }
    };

    addDarkToBody(session);

    return (
        <div className="toggleWrapper">
            <input type="checkbox" className="dn" id="dn" checked={darkModeEnabled} onChange={toggleDarkMode}/>
            <label htmlFor="dn" className="toggle">
                <span className="toggle__handler">
                  <span className="crater crater--1"/>
                  <span className="crater crater--2"/>
                  <span className="crater crater--3"/>
                </span>
                <span className="star star--1"/>
                <span className="star star--2"/>
                <span className="star star--3"/>
                <span className="star star--4"/>
                <span className="star star--5"/>
                <span className="star star--6"/>
            </label>
        </div>
    );
}

export default ToggleDark;
