import React, {useState} from 'react';
import Rodal from 'rodal';
import T from 'i18react';

import WorkCard from "../Components/WorkCard";

function Work(props) {

    const [openIngenico, setOpenIngenico] = useState(true);
    const [openMrm, setOpenMrm] = useState(false);
    const [openBlancLink, setOpenBlancLink] = useState(false);
    const [openTeravision, setOpenTeravision] = useState(false);
    const [openProsoft, setOpenProsoft] = useState(false);


    const onSelectIngenico = () => {
        setOpenIngenico(true);
        setOpenBlancLink(false);
        setOpenMrm(false);
        setOpenTeravision(false);
        setOpenProsoft(false);

    };
    const onSelectMrm = () => {
        setOpenMrm(true);

        setOpenBlancLink(false);
        setOpenIngenico(false);
        setOpenTeravision(false);
        setOpenProsoft(false);
    };
    const onSelectBlancLink = () => {
        setOpenBlancLink(true);

        setOpenIngenico(false);
        setOpenMrm(false);
        setOpenTeravision(false);
        setOpenProsoft(false);
    };
    const onSelectTeravision = () => {
        setOpenTeravision(true);

        setOpenIngenico(false);
        setOpenMrm(false);
        setOpenBlancLink(false);
        setOpenProsoft(false);
    };
    const onSelectProsoft = () => {
        setOpenProsoft(true);

        setOpenIngenico(false);
        setOpenMrm(false);
        setOpenBlancLink(false);
        setOpenTeravision(false);
    };

    return (
        <Rodal visible={props.open} onClose={props.toggle} width={600}>
            <h4>{T.translate('modals.work.title')}</h4>
            <div className="row work-container">
                <div className="col-12 col-md-3 border-work">
                    <div className="nav flex-column nav-pills"
                         role="tablist"
                         aria-orientation="vertical">
                        <span className={openIngenico ? "nav-link active": "nav-link"}
                              id="ingenico"
                              onClick={onSelectIngenico}>
                            Ingenico
                        </span>
                        <span className={openMrm ? "nav-link active": "nav-link"}
                              id="mrm"
                              onClick={onSelectMrm}>
                            MRM//Mcann
                        </span>
                        <span className={openBlancLink ? "nav-link active": "nav-link"}
                              id="blanclink"
                              onClick={onSelectBlancLink}>
                            BlancLink
                        </span>
                        <span className={openTeravision ? "nav-link active": "nav-link"}
                              id="teravision"
                              onClick={onSelectTeravision}>
                            Teravision Technologies
                        </span>
                        <span className={openProsoft ? "nav-link active": "nav-link"}
                              id="prosoft"
                              onClick={onSelectProsoft}>
                            Prosoft Solutions
                        </span>
                        <span className="scroller"/>
                    </div>
                </div>
                <div className="col-12 col-md-9">
                    <div className="tab-content" id="v-pills-tabContent">
                        <div className={openIngenico ? "tab-pane fade show active": "tab-pane fade"}
                             id="ingenico-tab"
                             role="tabpanel"
                             aria-labelledby="Ingenico-Tab">

                            <WorkCard
                                title={T.translate('modals.work.ingenico.title')}
                                text={T.translate('modals.work.ingenico.text')}
                                technologies={['HTML','React','CSS', 'JS / JQuery', 'PHP', 'SQL']}
                            />
                        </div>
                        <div className={openMrm ? "tab-pane fade show active": "tab-pane fade"}
                             id="mrm-tab"
                             role="tabpanel"
                             aria-labelledby="Mrm-Tab">
                            <WorkCard
                                title={T.translate('modals.work.mrm.title')}
                                text={T.translate('modals.work.mrm.text')}
                                technologies={['HTML','React','CSS', 'JS / JQuery', 'PHP', 'SQL']}
                            />
                        </div>
                        <div className={openBlancLink ? "tab-pane fade show active": "tab-pane fade"}
                             id="blanclink-tab"
                             role="tabpanel"
                             aria-labelledby="BlancLink-Tab">

                            <WorkCard
                                title={T.translate('modals.work.blanclink.title')}
                                text={T.translate('modals.work.blanclink.text')}
                                technologies={['HTML','Angular 1.4','CSS', 'JS / JQuery']}
                            />
                        </div>
                        <div className={openTeravision ? "tab-pane fade show active": "tab-pane fade"}
                             id="teravision-tab"
                             role="tabpanel"
                             aria-labelledby="Teravision-Tab">
                            <WorkCard
                                title={T.translate('modals.work.teravision.title')}
                                text={T.translate('modals.work.teravision.text')}
                                technologies={['HTML','PHP','CSS', 'JS / JQuery', 'SQL']}
                            />
                        </div>
                        <div className={openProsoft ? "tab-pane fade show active": "tab-pane fade"}
                             id="prosoft-tab"
                             role="tabpanel"
                             aria-labelledby="Prosoft-Tab">
                            <WorkCard
                                title={T.translate('modals.work.prosoft.title')}
                                text={T.translate('modals.work.prosoft.text')}
                                technologies={['HTML','PHP','CSS', 'JS / JQuery', 'SQL']}
                            />
                        </div>
                    </div>
                </div>
            </div>

        </Rodal>
    );
}

export default Work;
