import React from 'react';
import T from 'i18react';

function WorkCard(props) {

    return (
        <>
            <h5>{props.title}</h5>
            <hr/>
            <p>
                {props.text}
            </p>
            <div className="technologies">
                <strong>
                    {T.translate('modals.work.general.technologies')}:
                    <br/>
                </strong>
                {
                    props.technologies.map((element,index) => {
                        return <span key={index} className="badge badge-pill badge-info">{element}</span>
                    })
                }
            </div>
        </>
    );
}

export default WorkCard;
