import React, {useEffect, useState} from 'react';
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import { library } from '@fortawesome/fontawesome-svg-core';
import { fab } from '@fortawesome/free-brands-svg-icons'
import {faEnvelopeSquare, faMoon, faSun} from '@fortawesome/free-solid-svg-icons';
import T from 'i18react';

import {ReactComponent as Desk} from './Assets/images/desk.svg';
import {ReactComponent as MobileBg} from './Assets/images/phone-view.svg';
import {ReactComponent as Logo} from './Assets/images/logo.svg';

import About from './Modals/About';
import Alana from './Modals/Alana';
import Games from './Modals/Games';
import Mobile from './Modals/Mobile';
import Projects from './Modals/Projects';
import Studies from './Modals/Studies';
import Technologies from './Modals/Technologies';
import Work from './Modals/Work';

import ToggleDark from './Components/ToggleDark';

import enLang from './i18n/en.json';
import esLang from './i18n/es.json';

import './Assets/css/whiteBG.css';

library.add(fab);

function App() {
    const userLang = navigator.language.split('-')[0];
    const date = new Date();
    const timeFormatted = date.getHours() + ':' + (date.getMinutes()<10?'0':'') + date.getMinutes();

    let days = [];
    let months = [];
    if(userLang.toLowerCase() === 'es'){
        days = ['DOM','LUN','MAR','MIE','JUE','VIE','SAB'];
        months = ['ENE','FEB','MAR','ABR','MAY','JUN','JUL','AGO','SEP','OCT','NOV','DIC'];
        T.setTexts(esLang);
    }else{
        days = ['SUN','MON','TUE','WED','THU','FRI','SAT'];
        months = ['JAN','FEB','MAR','APR','MAY','JUN','JUL','AUG','SEP','OCT','NOV','DEC'];
        T.setTexts(enLang);
    }
    const dateFormatted = days[date.getDay()] + ', ' + date.getDate() + ' ' + months[date.getMonth() + 1];

    const [aboutOpen, setAboutOpen] = useState(false);
    const [alanaOpen, setAlanaOpen] = useState(false);
    const [gamesOpen, setGamesOpen] = useState(false);
    const [mobileOpen, setMobileOpen] = useState(false);
    const [projectsOpen, setProjectsOpen] = useState(false);
    const [studiesOpen, setStudiesOpen] = useState(false);
    const [techOpen, setTechOpen] = useState(false);
    const [workOpen, setWorkOpen] = useState(false);

    const [width, setWidth] = useState(window.innerWidth);
    const [currentTime, setCurrentTime] = useState(timeFormatted);
    const [currentDate, setCurrentDate] = useState(dateFormatted);
    const [marginElementsMobile, setMarginElementsMobile] = useState(0);

    const updateTimeIfChanged = () => {
        let time = new Date();
        let timeFormatted = time.getHours() + ':' + (time.getMinutes()<10?'0':'') + time.getMinutes();
        if(currentTime !== timeFormatted){
            setCurrentTime(timeFormatted);
        }
    };

    useEffect(() => {
        let svgAbout = document.getElementById('SVG-ABOUT-ME');
        let svgAlana = document.getElementById('SVG-ALANA');
        let svgGames = document.getElementById('SVG-GAMES');
        let svgMobile = document.getElementById('SVG-MOBILE-PROJECTS');
        let svgProjects = document.getElementById('SVG-PROJECTS');
        let svgStudies = document.getElementById('SVG-DIPLOMA');
        let svgTech = document.getElementById('SVG-TECHNOLOGIES');
        let svgWork = document.getElementById('SVG-WORK');
        let svgGroup = document.getElementById('Group_19');


        svgAbout.addEventListener('click', () => { setAboutOpen(true); });
        svgAlana.addEventListener('click', () => { setAlanaOpen(true); });
        svgGames.addEventListener('click', () => { setGamesOpen(true); });
        svgMobile.addEventListener('click', () => { setMobileOpen(true); });
        svgProjects.addEventListener('click', () => { setProjectsOpen(true); });
        svgStudies.addEventListener('click', () => { setStudiesOpen(true); });
        svgTech.addEventListener('click', () => { setTechOpen(true); });
        svgWork.addEventListener('click', () => { setWorkOpen(true); });

        if(width < 768){
            let svgLinkedin = document.getElementById('LinkedIn');
            let svgGithub = document.getElementById('Github');
            let svgEmail = document.getElementById('Email');
            svgLinkedin.addEventListener('click', (e) => { window.open('https://www.linkedin.com/in/jagltoro/', '_blank'); });
            svgGithub.addEventListener('click', (e) => { window.open('https://github.com/jagltoro', '_blank'); });
            svgEmail.addEventListener('click', (e) => { window.open('mailto:jagltoro@gmail.com?Subject=Hello', '_blank'); });

            setMarginElementsMobile((window.innerHeight - svgGroup.getBoundingClientRect().bottom)-15);
        }
        return () => {
            svgAbout.removeEventListener('click', () => {});
            svgAlana.removeEventListener('click', () => {});
            svgGames.removeEventListener('click', () => {});
            svgMobile.removeEventListener('click', () => {});
            svgProjects.removeEventListener('click', () => {});
            svgStudies.removeEventListener('click', () => {});
            svgTech.removeEventListener('click', () => {});
            svgWork.removeEventListener('click', () => {});
        }
    },[width]);

    useEffect(() => {
        const handleResize = () => setWidth(window.innerWidth);
        const intervalID = setInterval(updateTimeIfChanged, 10000);

        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
            clearInterval(intervalID)
        };

    }, []);

    const aboutToggle = () => { setAboutOpen(!aboutOpen); };
    const alanaToggle = () => { setAlanaOpen(!alanaOpen); };
    const gamesToggle = () => { setGamesOpen(!gamesOpen); };
    const mobileToggle = () => { setMobileOpen(!mobileOpen); };
    const projectsToggle = () => { setProjectsOpen(!projectsOpen); };
    const studiesToggle = () => { setStudiesOpen(!studiesOpen); };
    const techToggle = () => { setTechOpen(!techOpen); };
    const workToggle = () => { setWorkOpen(!workOpen); };

    return (
        <div className='container-fluid'>
            <div className="row">
                {
                    width >= 768 &&
                    <div className="col-md-2 no-padding">
                        <ul className="sidebar">
                            <li onClick={aboutToggle}>{T.translate('menu.about')}</li>
                            <li onClick={workToggle}>{T.translate('menu.work')}</li>
                            <li onClick={studiesToggle}>{T.translate('menu.studies')}</li>
                            <li onClick={projectsToggle}>{T.translate('menu.projects')}</li>
                            <li onClick={techToggle}>{T.translate('menu.tech')}</li>
                        </ul>
                        <div className="social">
                            <a aria-label="LinkedIn Page" href="https://www.linkedin.com/in/jagltoro/" target="_blank"
                               rel="noopener noreferrer">
                                <FontAwesomeIcon icon={['fab', 'linkedin']} size={width < 1000 ? '2x' : '3x'}/>
                            </a>
                            <a aria-label="Send Mail" href="https://github.com/jagltoro" target="_blank"
                               rel="noopener noreferrer">
                                <FontAwesomeIcon icon={['fab', 'github-square']} size={width < 1000 ? '2x' : '3x'}/>
                            </a>
                            <a aria-label="Send Mail" href="mailto:jagltoro@gmail.com?Subject=Hello">
                                <FontAwesomeIcon icon={faEnvelopeSquare} size={width < 1000 ? '2x' : '3x'}/>
                            </a>
                        </div>
                    </div>
                }
                <div className="col-12 col-md-10 desk-container">
                    {
                        width >= 768 &&
                        <>
                            <Logo className="logo-jg"/>
                            <Desk/>
                        </>
                    }
                    {
                        width < 768 &&
                        <div className="mobile-wrapper">
                            <Logo className="logo-jg"/>
                            <div className="bottom-bar" style={{marginBottom: marginElementsMobile}}/>
                            <MobileBg/>
                            <div className="time-container">
                                <span className="time">
                                    {currentTime} <br/>
                                    <small>{currentDate}</small>
                                </span>
                                <span className="day-night">
                                    {
                                        parseInt(currentTime) >= 20 &&
                                        <FontAwesomeIcon icon={faMoon} size="md" />
                                    }
                                    {
                                        parseInt(currentTime) < 20 &&
                                        <FontAwesomeIcon icon={faSun} size="md" />
                                    }
                                </span>
                                <ToggleDark/>
                            </div>
                        </div>
                    }
                    {
                        width >= 768 &&
                        <ToggleDark/>
                    }
                </div>

                    <About open={aboutOpen} toggle={aboutToggle} />
                    <Alana open={alanaOpen} toggle={alanaToggle} />
                    <Games open={gamesOpen} toggle={gamesToggle} />
                    <Mobile open={mobileOpen} toggle={mobileToggle} />
                    <Projects open={projectsOpen} toggle={projectsToggle} />
                    <Studies open={studiesOpen} toggle={studiesToggle} />
                    <Technologies open={techOpen} toggle={techToggle} />
                    <Work open={workOpen} toggle={workToggle} />
            </div>
        </div>
    );
}

export default App;
